<template>
  <div>
    <q-btn color="primary" label="Get Notification" @click="captureImage" />
  </div>
  <div>
    Permission:  {{ respuesta }}
  </div>
</template>

<script>
import { useQuasar } from 'quasar'
import { ref } from 'vue'
import { LocalNotifications } from '@capacitor/local-notifications'

export default {

  
  setup () {
    const $q = useQuasar()
    const respuesta =  ref('')
    const respuesta2 = ref('')
    const options = {
      title: 'Ejemplo mi estimado',
      body: 'Esto es un ejemplo',
      id: Date.now(),
      extra: {
        data: 'Algo mÃ¡s'
      }
    }

    function algo(){
      return new Promise(resolve => {
        Notification.requestPermission(result => {
          respuesta.value = result
          let granted = true;
          if (result === 'denied' || result === 'default') {
            granted = false;
          }
          resolve({ granted });
        });
      });
    }

    function captureImage () {
      algo().then(res => console.log(res)).catch(err => $q.notify(err))
      LocalNotifications.schedule({
        notifications: [options]
      }).then(() => $q.notify('Esto es una prueba'))
        .catch(err => {
          $q.notify(err)
        });
    }

    return {
      captureImage,
      respuesta,
      respuesta2,
    }
  }
}
</script>